import React, { Component } from 'react';

import './App.css';

import NavBar from './Components/NavBar/NavBar.jsx';

class App extends Component {

  render() {
    return (
      <div>
        <NavBar />
        {this.props.children}
     </div>
    );
  }
}

export default App;



